html {
  --active-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
}

body {
  margin: 0;
}

.container {
  justify-content: space-between;
  align-items: center;
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.logo {
  max-height: 40px;
}

.nav__list {
  text-align: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  min-width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__list-item:not(.nav__list-item:first-child) {
  margin-left: 10px;
}

.nav__list-item:not(.nav__list-item:last-child) {
  margin-right: 10px;
}

